import BirdeyeLogo from '@/assets/images/birdeye-logo.png'
import GeckoLogo from '@/assets/images/logo_CoinGecko@2x.png'
import PythLogo from '@/assets/images/logo_pyth.png'
import useWindowWidth from '@cetus/hooks/src/useWindowWidth'
import { Box, Image, ImageProps, Popover, PopoverBody, PopoverContent, PopoverTrigger } from '@chakra-ui/react'

export const MarketSourceMap: Record<string, string> = {
  pyth: 'Pyth Network',
  coingecko: 'CoinGecko',
  birdeye: 'Birdeye',
  cetus: 'Cetus'
}

const logoMap: Record<MarketType, string> = {
  pyth: PythLogo,
  coingecko: GeckoLogo,
  birdeye: BirdeyeLogo,
  cetus: '/images/img-logo@2x.png'
}

export type MarketType = 'pyth' | 'coingecko' | 'birdeye' | 'cetus'

interface MarketSourceProps extends ImageProps {
  market: MarketType
}

function MarketSource({ market, ...rest }: MarketSourceProps) {
  const { isApp } = useWindowWidth()
  return (
    <Popover isLazy trigger={isApp ? 'click' : 'hover'}>
      <PopoverTrigger>
        <Box flexShrink={0}>
          <Image w="16px" h="16px" src={logoMap[market]} {...rest} flexShrink={0} />
        </Box>
      </PopoverTrigger>
      <PopoverContent w="unset">
        <PopoverBody fontSize="12px">{MarketSourceMap[market]}</PopoverBody>
      </PopoverContent>
    </Popover>
  )
}

export default MarketSource
