import useTokenPrice from '@cetus/hooks/src/useTokenPrice'
import { Token } from '@cetus/types'
import { Decimal, formatNumber } from '@cetus/utils'
import { d } from '@cetusprotocol/common-sdk'
import { useMemo } from 'react'
import { useGetSwapPrice } from './useSwapHelper'

export function usePriceImpact(fromToken?: Token, toToken?: Token, fromValue?: string, toValue?: string, verifyPrice = false) {
  const { getTokenPrice } = useTokenPrice()

  const fromTokenPrice = getTokenPrice(fromToken?.coin_type, verifyPrice ? 10 * 1000 * 60 : undefined)
  const toTokenPrice = getTokenPrice(toToken?.coin_type, verifyPrice ? 10 * 1000 * 60 : undefined)

  /**
   * ÂÖëÊç¢‰ª∑Ê†º
   */
  const swapPrice = useGetSwapPrice(fromValue, toValue)

  /**
   * Â∏ÇÂú∫‰ª∑Ê†º
   */
  const marketPrice = useMemo(() => {
    if (toTokenPrice && fromTokenPrice) {
      return d(fromTokenPrice.price).div(toTokenPrice.price).toString()
    }
    return undefined
  }, [fromTokenPrice?.price, toTokenPrice?.price])

  /**
   * ‰ª∑Â∑Æ
   */
  const priceImpact = useMemo(() => {
    if (marketPrice && +swapPrice) {
      return d(swapPrice).sub(marketPrice).div(marketPrice).mul(100).toFixed(10)
    }

    return undefined
  }, [marketPrice, swapPrice])

  const priceImpactBasedOnMarket = useMemo(() => {
    if (marketPrice && +swapPrice) {
      return d(swapPrice).sub(marketPrice).div(marketPrice).mul(100).toFixed(10)
    }
    return undefined
  }, [marketPrice, swapPrice])

  /**
   * ‰ª∑Ê†ºÊèê‰æõÊ∫ê
   */
  const sources = useMemo(() => {
    const list = []
    if (fromTokenPrice) {
      list.push(fromTokenPrice.market)
    }

    if (toTokenPrice && !list.includes(toTokenPrice.market)) {
      list.push(toTokenPrice.market)
    }

    return list
  }, [fromTokenPrice?.market, toTokenPrice?.market])

  const showIncalculable = useMemo(() => {
    if (!fromTokenPrice || !toTokenPrice) {
      return true
    }
    if (fromTokenPrice.market === 'cetus' || toTokenPrice.market === 'cetus') {
      return true
    }
    return false
  }, [fromTokenPrice, toTokenPrice])

  /**
   * ÊòØÂê¶Â±ïÁ§∫ÊèêÁ§∫
   */
  const showPriceImpactTips = useMemo(() => {
    if (showIncalculable) {
      return false
    }
    if (priceImpact && d(priceImpact).lt(0)) {
      return d(priceImpact).abs().gt(5)
    }
    return false
  }, [priceImpact])

  /**
   * ÊòØÂê¶Â±ïÁ§∫Ë≠¶Âëä
   */
  const showPriceImpactWarn = useMemo(() => {
    if (showIncalculable) {
      return false
    }
    if (priceImpact && d(priceImpact).lt(0)) {
      return d(priceImpact).abs().gt(10)
    }
    return false
  }, [priceImpact, showIncalculable])

  const priceImpactTextInfo = useMemo(() => {
    let priceImpactText = undefined
    let textColor = 'primary_yellow'

    if (showIncalculable) {
      return {
        priceImpactText: 'Incalculable',
        textColor: 'primary_yellow'
      }
    }

    if (priceImpact) {
      // Â§ß‰∫é0
      if (d(priceImpact).gt(0)) {
        textColor = 'primary_green'
        // < 0.1%
        if (d(priceImpact).lt(0.1)) {
          priceImpactText = '< 0.1% cheaper than'
        } else {
          // > 0.1%
          priceImpactText = `${formatNumber(priceImpact, 2, true, Decimal.ROUND_UP)}% cheaper than`
        }
      } else if (d(priceImpact).eq(0)) {
        // Á≠â‰∫é0
        priceImpactText = '0%'
        textColor = 'primary_green'
      } else {
        // Â∞è‰∫é0
        const priceImpactAbs = d(priceImpact).abs()
        if (priceImpactAbs.lte(5)) {
          // [-5%,0%)
          textColor = 'primary_green'
          priceImpactText = `Within ${formatNumber(priceImpactAbs.toString(), 2, true, Decimal.ROUND_UP)}%`
        } else if (priceImpactAbs.lte(10)) {
          // [-10%,-5%)
          textColor = 'primary_yellow'
          priceImpactText = `${formatNumber(priceImpactAbs.toString(), 2, true, Decimal.ROUND_UP)}% more than`
        } else if (priceImpactAbs.lte(100)) {
          // [-100%,-10%)
          textColor = 'primary_red'
          priceImpactText = `${formatNumber(priceImpactAbs.toString(), 2, true, Decimal.ROUND_UP)}% more than`
        } else {
          // < -100%
          textColor = 'primary_red'
          priceImpactText = `> 100% more than`
        }
      }
    }
    return {
      priceImpactText,
      textColor
    }
  }, [priceImpact, showIncalculable])

  return {
    swapPrice,
    marketPrice,
    priceImpact,
    sources,
    showPriceImpactTips,
    showPriceImpactWarn,
    priceImpactTextInfo,
    priceImpactBasedOnMarket,
    showIncalculable
  }
}
